    showBkgCashmemoPaidAutoGst(): void {
        //        alert( 'am here 3' );
        this.printDto.mainstation = this.userDataDtoReturnSession.mainAdminStation;
        //        alert( 'am here 3 check the Mainstation' +this.printDto.mainstation);
        this.lrDtoForBookingAutoGst = this.printDto;
        //      hide the pop up pending;
        this.showBkg = false;
        this.viewLrEntryBkgCashmemoPrintAutoGSt = true;
        localStorage.clear();
//        console.log( this.lrDtoForBookingAutoGst );
        localStorage.setItem( 'printBkgCashmemoAutoGst', JSON.stringify( this.lrDtoForBookingAutoGst ) );
        window.addEventListener( 'afterprint', ( onclick ) => {
//            console.log('hai',this.getMode );
            if ( this.viewLrEntryBkgCashmemoPrintAutoGSt ) {
                this.viewLrEntryBkgCashmemoPrintAutoGSt = false;
              
                if ( this.getMode != '' && this.getMode == 'lrEntry' ) {
//                    console.log('22');
                    this.showBkg = false;
                }else{
//                    console.log('21');
                    // here this showBkg need be true
                    this.showBkg = false;
                }
                this.clearFields();
                window.setTimeout( function() {
                    $( "#lrNumber" ).val('');
                }, 100 );
              
                
            }
        } );
    }
