    lrNumberOnEnter(e) {
        if (e.keyCode == 13) {
            this.lrNumberGetDtls()
        }
    }

    lrNumberGetDtls() {
        this.validateEnteredLrNumber = $("#lrNumber").val();
        console.log(this.validateEnteredLrNumber);
        if (this.validateEnteredLrNumber == null || this.validateEnteredLrNumber == '') {
            swal({
                title: "Mandatory Fields",
                text: "Please Enter LR Number!",
                icon: "warning",
                closeOnClickOutside: true,
                closeOnEsc: true,
            }).then(function () {
                swal.close();
                window.setTimeout(function () {
                    $('#lrNumber').focus();
                }, 100);

            });
        } else {
            this.getLrNumberDetails();
        }
    }

    getMethodForGetLrNumberDtls() {
        this.lrDtoForGetLrNoDtls = new LRDto();
        this.enteredLrNumber = $("#lrNumber").val();
        this.lrDtoForGetLrNoDtls.companyId = this.userDataDtoReturnSession.companyId;
        this.lrDtoForGetLrNoDtls.lrNumber = this.enteredLrNumber;
        this.lrDtoForGetLrNoDtls.stocksAt = this.userDataDtoReturnSession.office;
    }

    getLrNumberDetails() {
        this.getMethodForGetLrNumberDtls();
        this.memoService.getLrDetailsForCashMemoService(this.lrDtoForGetLrNoDtls).subscribe(
            (response) => {
                console.log(response);
                if (response.length == 0) {
                    swal("Record Not Found", "No records found for Entered Lr Number Details", "warning");
                } else {
                    this.returnLrNumberDtls = new LRDto();
                    this.responseOfGetLrNumberDtls = response;
                    this.setLrFieldsDetails(this.responseOfGetLrNumberDtls);
                }
            }), (error) =>
                swal("Server Error", "Problem occur while getting Entered Lr Number Details", "error"),
            () => console.log('done');
    }

    clearFields() {
        $('input[type="text"],input[type="number"]').val('');
        this.clearAllAfterSave();
    }

    setLrFieldsDetails(dataFroLrFields) {
        console.log(dataFroLrFields);
        //LR Details
        $("#destinationId").val(dataFroLrFields.destination);
        $("#bookingDate").val(moment(dataFroLrFields.bookingDate).format("YYYY-MM-DD"));
        $("#invoiceNumber").val(dataFroLrFields.invoiceNumber);
        $("#invoiceDate").val(moment(dataFroLrFields.invoiceDate).format("YYYY-MM-DD"));
        $("#commodity").val(dataFroLrFields.column2);
        $("#sourceStation").val(dataFroLrFields.source);
        $("#agentName").val(dataFroLrFields.agentName);
        $("#noOfArticle").val(dataFroLrFields.totalArticles);
        $("#actualWeight").val(dataFroLrFields.actualWeight);
        $("#chargedWeight").val(dataFroLrFields.chargedWeight);


        //Consignor Details
        $("#consignorName").val(dataFroLrFields.consignorName);
        $("#consignorAddress").val(dataFroLrFields.consignorAdress);
        $("#consignorStateNameId").val(dataFroLrFields.consignorState);
        $("#consignorStateCode").val(dataFroLrFields.consignorStateCode);
        $("#consignorGstNumber").val(dataFroLrFields.gstNoConsignor);
        $("#consignorMobileNumber").val(dataFroLrFields.consignorMobileNo);
        //Consignee Details
        $("#consigneeName").val(dataFroLrFields.consigneeName);
        $("#consigneeAddress").val(dataFroLrFields.consigneeAdress);
        $("#consigneeStateNameId").val(dataFroLrFields.consigneeState);
        $("#consigneeStateCode").val(dataFroLrFields.consigneeStateCode);
        $("#consigneeGstNumber").val(dataFroLrFields.gstNoConsignee);
        $("#consigneeMobileNumber").val(dataFroLrFields.consigneeMobileNo);
        //Billing Details
        $("#toPay").val(dataFroLrFields.toPay);
        $("#paid").val(dataFroLrFields.paid);
    }
